.toastify {
  z-index: 999;
  position: fixed;
  padding: 4px;
  width: 320px;
  box-sizing: border-box;
  color: #fff; }

.toastify-content {
  position: relative;
  min-height: 48px;
  margin-bottom: 1rem;
  padding: 15px;
  padding-left: 50px;
  border-radius: 1px;
  opacity: 0.8;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,.16), 0 2px 10px 0 rgba(0,0,0,.12);
  display: flex;
  justify-content: space-between;
  max-height: 800px;
  overflow: hidden;
  font-family: sans-serif;
  cursor: pointer; 
    background-position: 15px center;
  background-repeat: no-repeat;}

.toastify-content:hover {
  opacity: 1;
  transition: .45s;
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  filter: alpha(opacity=100);
  cursor: pointer;
}

.toastify-content:before {
  position: absolute;
  top: 50%;
  left: 15px;
  margin-top: -14px;
  display: block;
  font-family: 'Notification';
  width: 28px;
  height: 28px;
  font-size: 28px;
  text-align: center;
  line-height: 28px;
}

.toastify-content--default {
  background: #fff;
  color: #aaa; 
}

.toastify-content--success {
  background-color: #00C851;
  background-image: url("data:image/png;base64,") !important;
}
.toastify-content--error {
  background-color: #F00;
  background-image: url("data:image/png;base64,") !important;
}
.toastify-content--info {
  background-color: #33b5e5;
  background-image: url("data:image/png;base64,") !important;
}
.toastify-content--warning {
  background-color: #FF8800;
  background-image: url("data:image/png;base64,") !important;
}


